export interface IPropertyPaneCheckboxProps {
  /**
   * Label to display next to the checkbox.
   */
  text?: string;

  /**
   * Whether the checkbox is checked or not.
   * @defaultvalue false.
   */
  isChecked?: boolean;

  /**
   * Whether the checkbox is enabled or not.
   * @defaultvalue true.
   */
  isEnabled?: boolean;
}
